<template>
  <div id="app">
    <app-header></app-header>
    <router-view/>
    <app-footer></app-footer>
    <cookie-law :buttonText="'Aceptar'">
      <div slot="message">
        Una cookie es un pequeño archivo de información que se guarda en su navegador cada vez que visita nuestra web. La utilidad de las cookies es guardar el historial de su actividad en nuestra para que, cuando la visite nuevamente, pueda identificarle y configurar su contenido en base a sus hábitos de navegación, identidad y preferencias. Pueden ser aceptadas, rechazadas, bloqueadas y borradas, según desee. En caso de que las rechace no podremos asegurarle el correcto funcionamiento de nuestra web. Más información en el apartado <router-link to="/politica-de-cookies">política de cookies</router-link>.
      </div>
    </cookie-law>
  </div>
</template>

<script>

  require('waypoints/lib/noframework.waypoints.js')

  import header from '@/components/parts/header'
  import footer from '@/components/parts/footer'
  import CookieLaw from 'vue-cookie-law'


  export default {
    name: 'App',
    components : {
      'app-header' : header,
      'app-footer' : footer,
      'cookie-law' : CookieLaw
    },
    mounted (){
      this.waypoints();
    },
    watch : {
      '$route' : function(){

        let that = this;

        setTimeout(function(){
          that.waypoints();
        },100);
      }
    },
    methods : {
      waypoints : function() {


        $(".waypoints-instant").addClass('animated fadeInUp');

        var waypoints = document.getElementsByClassName('waypoints');

        for (var i = 0; i < waypoints.length; i++) {

          new Waypoint({
            element: waypoints[i],
            offset : '100%',
            handler: function() {
              var element = this.key.split('-')[1];
              var effect = this.element.attributes.getNamedItem('data');
              (effect != undefined) ? effect = effect.value : effect = "fadeIn";
              this.element.classList.add("animated", effect)  
            }
          });

        }

      }

    }
  }
</script>

<style>
@import './assets/css/styles.css';
@import './assets/css/slick.css';
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700');
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.min.css);
</style>



